<template>
  <div style="font-size: 13px; font-family: Verdana; float: left">
    <h1>Books Issued Report</h1>
    <p></p>
    <div>
      <h2>Total books checked out: {{this.dataAdapter.totalrecords}}</h2>
    </div>
    <p></p>
    <JqxListBox
      v-if="this.displayListBox"
      style="float: right"
      @checkChange="myListBoxOnCheckChange($event)"
      :width="200"
      :height="200"
      :source="listBoxSource"
      :checkboxes="true"
    ></JqxListBox>
    <JqxButton
      style="float: right"
      :theme="'material'"
      @click="columnsBtnOnClick()"
    >{{ this.columnsBtnTxt }}</JqxButton>
    <JqxGrid
      ref="myGrid"
      :theme="'energyblue'"
      :width="width"
      :source="dataAdapter"
      :filterable="true"
      :showfilterrow="true"
      :sortable="true"
      :columns="columns"
      :columnsresize="true"
      :pageable="true"
      :sorting="true"
      :rendered="columnsBtnOnClick"
    ></JqxGrid>

    <div style="height: 60px; margin-top: 15px;">
      <div style="float: left; margin-left: 15px">
        <JqxButton style="float: left" :theme="'material'" @click="removeFilter()">Remove Filter</JqxButton>
      </div>

      <div style="float: left; margin-left: 15px">
        <JqxButton :theme="'material'" @click="pdfBtnOnClick()">Export to PDF</JqxButton>
      </div>

      <div style="float: left; margin-left: 15px">
        <JqxButton :theme="'material'" @click="excelBtnOnClick()">Export to Excel</JqxButton>
      </div>
    </div>
  </div>
</template>
 
 <script>
import JqxGrid from "jqwidgets-scripts/jqwidgets-vue/vue_jqxgrid.vue";
import JqxButton from "jqwidgets-scripts/jqwidgets-vue/vue_jqxbuttons.vue";
import JqxListBox from "jqwidgets-scripts/jqwidgets-vue/vue_jqxlistbox.vue";

export default {
  name: "Report",
  components: {
    JqxGrid,
    JqxButton,
    JqxListBox
  },
  data() {
    return {
      width: "800",
      displayListBox: false,
      columnsBtnTxt: "Display Columns",
      dataAdapter: new jqx.dataAdapter(this.source),
      columns: [
        {
          text: "Student ID",
          datafield: "studentID",
          width: "10%",
          sortable: true
        },
        {
          text: "First Name",
          datafield: "firstName",
          width: "10%",
          sortable: true
        },
        {
          text: "Last Name",
          datafield: "lastName",
          width: "10%",
          sortable: true
        },
        { text: "ISBN", datafield: "isbn", width: "10%", sortable: true },
        {
          text: "Book Name",
          datafield: "bookName",
          width: "15%",
          sortable: true
        },
        { text: "Author", datafield: "author", width: "10%", sortable: true },
        {
          text: "Book Code",
          datafield: "bookCode",
          width: "10%",
          sortable: true
        },
        {
          text: "Checked Out Date",
          datafield: "checkedOutDate",
          filtertype: "range",
          cellsformat: "MM/dd/yy",
          width: "10%",
          sortable: true
        },
        {
          text: "Due Date",
          datafield: "dueDate",
          cellsformat: "MM/dd/yy",
          width: "10%",
          sortable: true
        }
      ],
      listBoxSource: [
        { label: "Student ID", value: "studentID", checked: true },
        { label: "First Name", value: "firstName", checked: true },
        { label: "Last Name", value: "lastName", checked: true },
        { label: "ISBN", value: "isbn", checked: true },
        { label: "Author", value: "author", checked: true },
        { label: "Book Name", value: "bookName", checked: true },
        { label: "Book Code", value: "bookCode", checked: true },
        { label: "Checked Out Date", value: "checkedOutDate", checked: true },
        { label: "Due Date", value: "dueDate", checked: true }
      ]
    };
  },
  beforeCreate() {
    this.source = {
      url: process.env.VUE_APP_ROOT_API + "/api/report",
      contenttype: "application/json",
      datatype: "json",
      datafields: [
        { name: "studentID", type: "string" },
        { name: "firstName", type: "string" },
        { name: "lastName", type: "string" },
        { name: "isbn", type: "number" },
        { name: "bookName", type: "string" },
        { name: "author", type: "string" },
        { name: "bookCode", type: "string" },
        { name: "checkedOutDate", type: "date" },
        { name: "dueDate", type: "date" },
        { name: "returnDate", type: "date" }
      ],
      id: "id",
      sortcolumn: "studentID",
      sortdirection: "asc"
    };
  },
  methods: {
    removeFilter: function() {
      this.$refs.myGrid.clearfilters();
    },
    pdfBtnOnClick: function() {
      this.$refs.myGrid.exportdata("pdf", "jqxGrid");
    },

    excelBtnOnClick: function() {
      this.$refs.myGrid.exportdata("xls", "jqxGrid");
    },

    columnsBtnOnClick: function() {
      if (!this.displayListBox) {
        this.columnsBtnTxt = "Close";
        this.displayListBox = true;
      } else {
        this.columnsBtnTxt = "Display Columns";
        this.displayListBox = false;
      }
    },
    myListBoxOnCheckChange: function(event) {
      this.$refs.myGrid.beginupdate();
      if (event.args.checked) {
        this.$refs.myGrid.showcolumn(event.args.value);
      } else {
        this.$refs.myGrid.hidecolumn(event.args.value);
      }
      this.$refs.myGrid.endupdate();
    }
  }
};
</script>
